.wrapper {
  padding: 15vh 5vw;
  min-width: calc(90vw - $scrollbar-approx);
  min-height: 70vh; }
  .wrapper * {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default; }
  .wrapper a, .wrapper a:visited {
    color: inherit;
    text-decoration: underline; }
    .wrapper a, .wrapper a *, .wrapper a:visited, .wrapper a:visited * {
      cursor: pointer; }
  .wrapper span {
    font-size: 11pt;
    font-weight: 400; }
  .wrapper [data-visible="opaque"] {
    opacity: 1; }
  .wrapper [data-visible="translucent"] {
    opacity: 0.4; }
  .wrapper [data-visible="transparent"] {
    opacity: 0; }
  .wrapper .header {
    margin-bottom: 2rem;
    display: inline-block;
    z-index: 10;
    position: relative; }
    .wrapper .header h1 {
      font-size: calc(33pt + 2vw);
      margin: 0 0 .5rem 0;
      font-weight: 700;
      word-break: keep-all; }
    .wrapper .header h2 {
      font-size: calc(16.5pt + 1vw);
      margin: 0 0 .5rem 0;
      font-weight: 500;
      word-break: keep-all; }
    .wrapper .header span {
      color: #b5897f; }
  .wrapper .main {
    display: block;
    z-index: 10;
    position: relative; }
    .wrapper .main span {
      white-space: pre;
      text-shadow: 0 0 5px var(--c-back); }
  .wrapper .footer {
    display: contents; }
    .wrapper .footer .blhx {
      position: fixed;
      z-index: 0;
      bottom: 10vh;
      right: 5vw;
      max-width: 50vw;
      height: auto;
      max-height: 70vh;
      transition: opacity .4s ease-in-out; }
